

.warehouse-list .collection-item{
    cursor: pointer;
}

.warehouse-list .row {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	-ms-align-items: center;
	align-items: center;
}

.warehouse-list .city-name {
    font-size: 25px;
    padding-left: 25px;
    color: #d0181e;
}

.warehouse-list .collection h3 {
	color: #d0181e;
}

.warehouse-list .collection {
	border: none;
}

@media (max-width: 600px) {
	.warehouse-list .row {
		flex-direction: column;
	}
}